.parcours {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  height: 100vh;
  width: 80vw;
  & nav {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 150px;
    & ul {
      display: flex;
      justify-content: space-evenly;
      align-items: center;
      list-style: none;
      width: 100%;
      height: 50%;
      background-color: black;
      color: white;
      border-radius: 15px;
      padding: 5px 20px;
      & li {
        text-transform: uppercase;
        font-size: 1.1rem;
        transition: color .5s;
        &:hover {
          cursor: pointer;
          color: $primary-color;
        }
      }
      & .li-separation {
        width: 1px;
        height: 100%;
        background-color: whitesmoke;
      }
    }
  }
  & .parcours-content {
    width: 100%;
    height: 100%;
      display: flex;
      justify-content: space-around;
      align-items: center;
      & img {
        height: 70%;
        width: 30%;
        border-radius: 50%;
      }
    }
  & .horaires-content {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    color: white;
    font-weight: bold;
    font-size: 3rem;
    text-shadow: 2px 2px 1px black;
  }
  & .parcours-button-return {
    @include button;
    width: 200px;
  }
}
.active {
  color: $primary-color;
}